.wrapLogo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0 1rem;
  cursor: pointer;
}

.anonimosLogo {
  color: #020202;
  font-family: "Prometheanexpand", Times, serif;
  font-size: 40px;
}

.developmentLogo {
  color: gray;
  font-family: "Helvetica Neue", Times, serif;
  font-size: 18px;
}

@media (max-width: 767px) {
  .anonimosLogo {
    font-size: 30px;
    margin-bottom: 5px;
  }

  .wrapLogo {
    flex-direction: column;
  }
}
